html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;

  display: flex;
  flex-direction: column;
  min-width: $mobile-width;
  min-height: 100vh;
  margin: 0;
  padding: 0;

  font-family: $default-font;
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  font-style: normal;
  text-align: left;
  color: $text-color;

  background-color: $main-color;
}

a {
  text-decoration: none;

  &:hover,
  &:focus {
    @include transition;

    opacity: 0.5;
  }

  &:active {
    @include transition;

    opacity: 0.7;
  }
}

img {
  max-width: 100%;
}
